import { Box, Paper, Stack } from "@mui/material";
import { useState } from "react";
import ChatBox from "./ChatBox";

const NonUrgentchatList = ({ onClick, contacts, id }) => {
  const displayChatBox = (contact) => {
    return (
      <ChatBox key={contact.id} contact={contact} onClick={onClick} id={id} />
    );
  };

  return (
    <>
      <Paper
        elevation={6}
        sx={{
          backgroundColor: "white",
          minHeight: "20vh",
          maxHeight: { xs: "fit-content", md: "70vh", lg: "80vh" },
          overflow: { xs: "visible", md: "auto" },
          borderRadius: 4,
          borderTopRightRadius: 2,
          borderBottomRightRadius: 2,
          p: 2,
          m: 1,
          ml: { xs: 0, md: -5 },
          mb: { xs: 2, md: 5 },
        }}
      >
        <Stack spacing={1}>
          {
            // Render list of chats that have no notification
            contacts?.length > 0 ? (
              contacts?.map(displayChatBox)
            ) : (
              <Box sx={{ mr: 4, textAlign: "center" }}>
                Sorry no match was found :/
              </Box>
            )
          }
        </Stack>
      </Paper>
    </>
  );
};

export default NonUrgentchatList;
