import { Box, Typography, Paper, styled, Divider } from "@mui/material";
import UserAvatar from "./UserAvatar";
import messageService from "../services/messages";
import notifService from "../services/notif";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

const ChatBox = ({ contact, id }) => {
  const navigate = useNavigate();
  const [notif, setNotif] = useState(null);

  useEffect(() => {
    const fetchNotif = async () => {
      // Return the notif where receiver is the client and sender is the contact
      const notifObj = await notifService.checkNotif(contact.id, id);
      setNotif(notifObj);
    };

    fetchNotif();
  }, [id, contact]);

  const capitalize = () => {
    let firstname = contact.firstName; //.toUpperCase()
    let lastname = contact.lastName; //.toUpperCase()

    return `${firstname} ${lastname}`;
  };

  // When Clicked, load contact messages
  const handleChatBoxClick = async (e) => {
    // Ignore holding down the chatbox
    if (e.type === "keydown") {
      return;
    }

    // Check for notif where client user is the receiver and
    // contact user is the sender
    const notif = await notifService.checkNotif(contact.id, id);

    // TODO: If notification exists and the count is not zero, update notif count to zero
    if (notif && notif.count !== 0) {
      const newNotifObj = { ...notif, count: 0 };
      const newNotif = await notifService.update(notif.id, newNotifObj);
      setNotif(newNotif);
    }

    // TODO: Update notification change to the server
    navigate(`/chat/room/${id}/${contact.id}`);
  };

  // Allow the chatbox to be elevated on mouse hover
  const MyPaper = styled(Paper)(({ theme }) => ({
    "&:hover": {
      boxShadow: theme.shadows[24],
    },
  }));

  return (
    <>
      <MyPaper
        elevation={0}
        onClick={handleChatBoxClick}
        sx={{
          display: "flex",
          flexDirection: "row",
          flexWrap: "nowrap",
          height: { xs: "20vh", md: "13vh", lg: "16vh" },
          p: 2,
          alignItems: "center",
          borderRadius: 3,
        }}
      >
        {
          <Box
            sx={{
              pr: 2,
              display: "flex",
            }}
          >
            <Box>{<UserAvatar person={contact} />}</Box>
            {notif?.count > 0 && (
              <Box>
                <Box sx={{ height: 30 }}></Box>
                <Box
                  sx={{
                    height: 15,
                    width: 15,
                    backgroundColor: "lightgreen",
                    borderRadius: 10,
                    border: "1px solid white",
                    bottom: "5%",
                    ml: -2,
                    position: "relative",
                    left: 0,
                    right: 0,
                  }}
                ></Box>
              </Box>
            )}
          </Box>
        }
        {
          <Box sx={{ flexGrow: 1 }}>
            <Box>
              <Typography
                fontWeight="bold"
                fontSize={17}
                fontFamily="Arial, sans-serif"
              >
                {capitalize()}
              </Typography>
            </Box>
            <Box>
              <Typography mb={1} color="GrayText" fontSize={13} sx={{ mt: 1 }}>
                {
                  //TODO: the string of the newest message can be gotten by
                  // accesing the latest message of the current person object
                }
                {Boolean(notif?.message) ? notif?.message : "No message yet :)"}
              </Typography>
            </Box>
          </Box>
        }
        {
          <Box>
            <Box>
              <Typography fontWeight="lighter" fontFamily="Arial, sans-serif">
                {
                  //TODO: the date of the newest message can be gotten by
                  // accesing the latest message of the current person object
                }
                {notif?.date}
              </Typography>
            </Box>
            <Box height={32}>
              {notif?.count > 0 && (
                <Box
                  fontFamily="Arial, sans-serif"
                  sx={{
                    backgroundColor: "lightsalmon",
                    color: "white",
                    width: 25,
                    ml: 2,
                    textAlign: "center",
                    border: "1px solid lightsalmon",
                    borderRadius: 15,
                    mt: 1,
                  }}
                >
                  {notif?.count}
                </Box>
              )}
            </Box>
          </Box>
        }
      </MyPaper>
      <Divider
        sx={{
          display: { xs: "", md: "" },
        }}
      />
    </>
  );
};

export default ChatBox;
