import { Box, Stack } from "@mui/material";
import { useState } from "react";
import ChatBox from "./ChatBox";

const UrgentChatList = ({ onClick, contacts, id }) => {
  const displayChatBox = (contact) => {
    return (
      <ChatBox key={contact.id} contact={contact} onClick={onClick} id={id} />
    );
  };

  return (
    <>
      <Stack
        id="chatlist"
        sx={{
          backgroundColor: "white",
          maxHeight: { xs: "fit-content", md: "80vh" },
          minHeight: "20vh",
          overflow: { xs: "visible", md: "auto" },
          borderRadius: 4,
          borderTopRightRadius: 2,
          borderBottomRightRadius: 2,
          p: 2,
        }}
      >
        {
          // Render list of chats that have notification
          contacts?.length > 0 ? (
            contacts?.map(displayChatBox)
          ) : (
            <Box sx={{ mr: 4, textAlign: "center" }}>
              Sorry no match was found :/
            </Box>
          )
        }
      </Stack>
    </>
  );
};

export default UrgentChatList;
