import { useEffect, useState } from "react";
import userService from "../services/user";
import UrgentChatList from "./UrgentChatList";
import NonUrgentchatList from "./NonUrgentChatList";

const ChatList = ({ contacts, userState: { user } }) => {
  // Contacts that have an active notification
  const [yesNotif, setYesNotif] = useState([]);

  // Contcats that have no active notification
  const [noNotif, setNoNotif] = useState([]);

  // TODO: Show only the chat blocks if route at '/chat/room/:userId'
  // Else if route at [messageblock] route, show only message block

  useEffect(() => {
    const getNotifs = async () => {
      // Get the user object and select all it's notification
      const userObj = await userService.getUser(user.id);

      // Create an array that describes the contacts with active notification
      const notifArr = userObj.notif.map((obj) => {
        // Check if the notif count is actually more than 0
        // and the receiver of the notification is the client user
        if (obj.count > 0 && obj.receiver === user.id) {
          return obj.sender;
        }
        return undefined;
      });

      setYesNotif(contacts.filter((user) => notifArr.includes(user.id)));
      setNoNotif(contacts.filter((user) => !notifArr.includes(user.id)));
    };
    getNotifs();
  }, [user, contacts]);

  return (
    <>
      <UrgentChatList contacts={yesNotif} id={user.id} />
      <NonUrgentchatList contacts={noNotif} id={user.id} />
    </>
  );
};

export default ChatList;
